<template>
  <div class="editor">
    <mavon-editor
      :style="editorStyle"
      :toolbars="toolbars"
      :subfield="false"
      @save="handleSave"
      :default_open='defaultOpen'
      :toolbarsFlag='toolbarsFlag'
      :value='value'
      :placeholder='placeholder'
      @imgAdd="handleAddImg"
      @imgDel="handleDelImg"
      ref="md"
      @change='handleChange' ></mavon-editor>
  </div>
</template>

<script>
  // :  
  // TODO: 
  // : 
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  // import markdownItComponent from './markdownPlugin/markdown-it-vue-component.js'
  import api from '@/api'

  // const md = mavonEditor.mixins[0].data().s_markdown
  // md.use(markdownItComponent)

  export default {
    name: 'editor',
    props: {
      isRead: {
        type: Boolean,
        default: false
      },
      value: String,
      placeholder: String
    },
    data () {
      return {
        toolbars: {
          bold: true, // 
          italic: true, // 
          header: true, // 
          underline: true, // 
          strikethrough: true, // 
          mark: true, // 
          superscript: true, // 
          subscript: true, // 
          quote: true, // 
          ol: true, // 
          ul: true, // 
          link: true, // 
          imagelink: true, // 
          code: true, // code
          table: true, // 
          fullscreen: true, // 
          readmodel: true, // 
          htmlcode: true, // html
          help: true, // 
          /* 1.3.5 */
          undo: true, // 
          redo: true, // 
          trash: true, // 
          save: true, // eventssave
          /* 1.4.2 */
          navigation: true, // 
          /* 2.1.8 */
          alignleft: true, // 
          aligncenter: true, // 
          alignright: true, // 
          /* 2.2.1 */
          subfield: true, // 
          preview: true // 
        },
        editorStyle: {
          height: '100%',
          minHeight: 'auto',
          overflow: 'hiddne'
        }
      }
    },
    computed: {
      defaultOpen () {
        if (this.isRead) {
          return 'preview'
        }
        return 'edit'
      },
      toolbarsFlag () {
        return !this.isRead
      }
    },
    components: {
      'mavon-editor': mavonEditor
    },
    methods: {
      handleSave (val, render) {
        this.$emit('save', val, render)
      },
      handleChange (val, render) {
        this.$emit('change', val, render)
      },
      handleAddImg (pos, file) {
        console.log('', pos, file)
        let formdata = new FormData()
        formdata.append('file', file)
        api.uploadImg(formdata)
        .then((res) => {
          console.log(res)
          this.$refs.md.$img2Url(pos, res.url)
          console.log(this.$refs.md)
        })
      },
      handleDelImg (pos) {
        console.log('')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .editor {
    flex: 1;
    overflow: auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
  }
  .editor .v-show-content img {
    max-width: 100%;
    height: auto;
  }
</style>



